import type { NextPage, GetStaticPropsContext } from "next";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import { useTranslations } from "next-intl";

import ResponsiveAppBar from "../src/components/ResponsiveAppBar";
import React, { useMemo } from "react";
import Head from "next/head";
import Paper from "@mui/material/Paper";

import { useTheme } from "../src/theme";
import SearchForm from "../src/components/SearchForm";
import { Footer } from "../src/components/Footer";
import Link from "../src/components/Link";

import { Typography } from "@mui/material";
import { useRouter } from "next/router";

const perPage = 250;

const Airlines: NextPage = ({ airlines }: any) => {
  const theme = useTheme();
  const t = useTranslations();
  const { query } = useRouter();
  const [from, to] = useMemo(() => {
    if (query?.page) {
      const start = perPage * Number(query.page);
      return [start, start + perPage];
    }

    return [0, 250];
  }, [query]);

  const hasMore = useMemo(() => {
    if (query?.page && to) {
      return to <= airlines.length;
    } else {
      return true;
    }
  }, [query, to, airlines.length]);

  const hasPrevious = useMemo(() => {
    if (from) {
      return from > 0;
    }
  }, [from]);

  return (
    <Box
      sx={{
        backgroundColor: theme.palette.mode === "light" ? "#F3F6F9" : "inherit",
      }}
    >
      <Head>
      <title>
          {t("airlines")} - {t("cheap-flights-and-airline-deals")} |
          Flightscanner
        </title>
      </Head>
      <ResponsiveAppBar />
      <Paper
        variant={theme.palette.mode === "light" ? "outlined" : "elevation"}
        sx={{ pb: 2, borderLeft: "none", borderRight: "none" }}
        square
      >
        <Typography
          variant="h3"
          gutterBottom
          sx={{ textAlign: "center", pt: 4 }}
        >
          {t("airlines")}
        </Typography>
        <SearchForm maxWidth="lg" showBooking={false} />
      </Paper>

      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Paper sx={{ p: 1 }}>
          <Box component="ul" sx={{ display: "flex", flexWrap: "wrap", listStyle: "none", p: 0 }}>
            {airlines.slice(from, to).map((city: any) => {
              const slug = city.slug;
              const name = city.name;
              return (
                <Box
                  key={name}
                  component="li"
                  sx={{ flex: { xs: "100%", sm: "50%", md: "0 0 33%" } }}
                >
                  <Link
                    sx={{ display: "block" }}
                    href={`/${t("routes-airline")}/${slug}`}
                    underline="hover"
                    color="inherit"
                  >
                    {name}
                  </Link>
                </Box>
              );
            })}
          </Box>
        </Paper>
        <Box sx={{ display: "flex", p: 1 }}>
          {hasPrevious && (
            <Link href={`/${t("routes-airlines")}?page=${from / perPage - 1}`} sx={{ color: 'inherit'}}>
              Previous
            </Link>
          )}
          {hasMore && (
            <Link
              href={`/${t("routes-airlines")}?page=${from / perPage + 1}`}
              sx={{
                display: "flex",
                flex: 1,
                justifyContent: "end",
                color: "inherit",
              }}
            >
              Next
            </Link>
          )}
        </Box>
      </Container>
      <Footer />
    </Box>
  );
};

export async function getStaticProps({ locale }: GetStaticPropsContext) {
  const airlines = require("../data/Airlines.json");

  return {
    props: {
      airlines,
      // You can get the messages from anywhere you like. The recommended
      // pattern is to put them in JSON files separated by language and read
      // the desired one based on the `locale` received from Next.js.
      messages: (await import(`../messages/index/${locale || 'en'}.json`)).default,
    },
  };
}

export default Airlines;
